import { userLogin } from '@/api/user'

export default {
    namespaced: true,
    state: {
        user: JSON.parse(localStorage.getItem('nhm-siapin-user')) || null,
        authenticated: localStorage.getItem('nhm-siapin-authenticated') || false,
        status: localStorage.getItem('nhm-siapin-auth-status') || false,
    },
    getters: {
        user: (state) => state.user,
        authenticated: (state) => state.authenticated,
        status: (state) => state.status,
    },
    mutations: {
        authDefault(state) {
            state.user = null;
            state.authenticated = false;
        },
        authSuccess(state, user){
            state.user = user;
            state.authenticated = true;
        },
        authStatus(state, status){
            state.status = status;
        }
    },
    actions: {
        async authRequest({commit}, userModel){
            commit('authDefault');
            let user = null;

            try{
                let response = await userLogin(userModel);
                let data = response.data;

                if(!data?.user){
                    localStorage.setItem('nhm-siapin-auth-status', data);
                    commit('authStatus', data);
                    return false;
                }else{
                    let userData = data?.user?.properties;

                    user = {
                        username: userData?.userprincipalname?.[0],
                        fullname: userData?.displayname?.[0],
                        department: (userData?.department != null) ? userData?.department?.[0] : '-',
                        token: data?.token,
                        role: "Admin",
                        authorization_id: 1,
                    }

                    localStorage.setItem('nhm-siapin-user', JSON.stringify(user));
                    localStorage.setItem('nhm-siapin-authenticated', true);
                    commit('authSuccess', user);
                }

                return true;
            }catch(e){
                console.error(e);
                return false;
            }
        },
        authLogout({commit}){
            localStorage.removeItem('nhm-siapin-user');
            localStorage.removeItem('nhm-siapin-authenticated');
            commit('authDefault');
            return true;
        },
    }
}