import { httpClient,  httpClientForm } from './httpClient';

const END_POINT = '/api/user';
const qs = require("query-string");

const userAll = () => {
	return httpClient.get( END_POINT + "/");
}
const userById = (id) => {
	return httpClient.get( END_POINT + "/" + id);
}
const userAdd = (data) => {
	return httpClient.post( END_POINT + "/", data);
}
const userUpdate = (id, data) => {
	return httpClient.put( END_POINT + "/" + id, data);
}
const userDelete = (id) => {
	return httpClient.delete( END_POINT + "/" + id);
}
const userLogin = (data) => {
	return httpClientForm.post( END_POINT + "/login", qs.stringify(data));
}

export {
	userAll,
	userById,
	userAdd,
	userUpdate,
	userDelete,
	userLogin,
}