import axios from 'axios'

axios.defaults.baseURL = process.env.VUE_APP_API_URL

const httpClient = axios.create({
	headers: {
		"Content-Type": "application/json",
		"Authorization": "Bearer " + JSON.parse(localStorage.getItem("nhm-siapin-user"))?.token
	}
});

const httpClientNoAuth = axios.create({
	headers: {
		"Content-Type": "application/json",
	}
});

const httpClientForm = axios.create({
	headers: {
		"Content-Type": "application/x-www-form-urlencoded"
	}
});

const httpClientFile = axios.create({
	headers: {
		"Content-Type": "multipart/form-data".Authorization,
		"Authorization": "Bearer " + JSON.parse(localStorage.getItem("nhm-siapin-user"))?.token
	}
});

export {
	httpClient,
	httpClientNoAuth,
	httpClientForm,
	httpClientFile,
}