<template>
	<div class="wrapper">
		<div class="back-wrap">
			<div class="row">
				<div style="width: 250px">
					<div class="p-2">
						<div class="row">
							<div class="col-md-6">
								<img :src="require('@/assets/images/logo.png')" class="img-logo v-align-middle ml-3 mr-4">	
							</div>
							<div class="col-md-6 text-right">
								<button class="btn right mt-3 btn-sm" id="btn-toggle"><i class="fas fa-bars"></i></button>	
							</div>
						</div>
					</div>
					<div class="left-menu sidebar col-dln-3" style="height: 90vh; overflow-y: auto;">
						<div class="pl-4 pr-2 mt-4">
							<div class="mb-4 p-2">
								<div class="row align-items-center">
									<div class="col-4">
										<img src="@/assets/images/user.png" class="img-fluid img-menu mb-2">
									</div>
									<div class="col-8">
										<div class="font-weight-bold">{{ user.name }}</div>
										<small class="text-main">{{ user.role }}</small>	
									</div>
								</div>
							</div>
							<SideMenu/>
						</div>
					</div>
				</div>
				<div class="col-dln-92">
					<div>
						<ul class="nav p-4 justify-content-end">
							<li class="nav-item" @click="logout">
								<a href="javascript:void(0)" class="nav-link text-white">
									Logout <i class="fas fa-sign-out-alt"></i>
								</a>
							</li>
						</ul>
					</div>
					<div class="col-dln-9">
						<div style="padding-bottom: 20px">
						<slot></slot>
						</div>
						<div class="mb-4"></div>
						<div class="credits" style="display: flex; justify-content: space-between; max-width: 98%;">
							<div>
								Copyright NHM 2020. All right reserved
							</div>
							<div>
								{{ appVers }}
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import SideMenu from '@/components/layouts/Navbar.vue';
	import { version } from '../../../package.json';

	export default {
		name: 'Dashboard',
		components: {
			SideMenu
		},
		data(){
			return {
				appVers: "v-",
				user: {
					name: null,
					role: null,
				}
			}
		},
		mounted(){
			this.appVers = `v${version}`;
			if(!this.$store.state.auth.authenticated){
				this.$router.push('/login');
			}else{
				let user = this.$store.state.auth.user;
				this.user = {
					name: user?.fullname,
					role: user?.role,
				}
			}

			$(window).on('resize', function() {
				$(".col-dln-3").css('width', 250+'px');
				$(".col-dln-92").css('width', $(document).width()-250+'px');
				$(".col-dln-9").css('width', $(document).width()-250+'px');
				$(".col-dln-9").css('margin-left', '1em');
			});
			$(".col-dln-3").css('width', 250+'px');
			$(".col-dln-92").css('width', $(document).width()-250+'px');
			$(".col-dln-9").css('width', $(document).width()-250+'px');
			$(".col-dln-9").css('margin-left', '1em');
			// $(".col-dln-9").css('padding-right', '1em');
			$("#btn-toggle").on('click', function(){
				if($(".col-dln-3").width() == 0){
					$(".col-dln-3").css('width', 250+'px');
					$(".col-dln-9").css('width', $(document).width()-250+'px');
					$(".col-dln-9").css('margin-left', '1em');
				}else{
					$(".col-dln-3").css('width', 0);
					$(".col-dln-9").css('width', ($(document).width()-30)+'px');
					$(".col-dln-9").css('margin-left', '-210px');
				}
			})
			$(".left-menu .nav-drop").on("click", function(){
				$(this).toggleClass('active');
				$(this).parent().prevAll('li').find('.show').removeClass('show');
				$(this).parent().nextAll('li').find('.show').removeClass('show');
				$(this).parent().prevAll('li').find('.nav-drop.active').removeClass('active');
				$(this).parent().nextAll('li').find('.nav-drop.active').removeClass('active');
				$(this).next('ul').toggleClass('show');
			});
			// get active menu
			var menus = $("li[data-toggle='menu']");
			var current = this.$router.currentRoute.fullPath;
			for(var i = 0; i<menus.length; i++){
				var url = $(menus[i]).attr('data-url');
				if(current == url){
					$(menus[i]).addClass('active');
				}
			}
			// get active a dropdown
			var anchors = $("a[data-toggle='menu']");
			for(var i = 0; i<anchors.length; i++){
				var url = $(anchors[i]).attr('data-url');
				// check or
				if(url.includes('||')){
					var urls = url.split('||');
					for(var ii = 0; ii < urls.length; ii++){
						if(current.includes(urls[ii])){
							$(anchors[i]).addClass('active');
						}	
					}
				}else if(current.includes(url)){
					$(anchors[i]).addClass('active');
				}
			}
			// get active ul dropdown
			var dropdowns = $("ul[data-toggle='menu']");
			for(var i = 0; i<dropdowns.length; i++){
				var url = $(dropdowns[i]).attr('data-url');
				if(url.includes('||')){
					var urls = url.split('||');
					for(var ii = 0; ii < urls.length; ii++){
						if(current.includes(urls[ii])){
							$(dropdowns[i]).addClass('show');
						}	
					}
				}else if(current.includes(url)){
					$(dropdowns[i]).addClass('show');
				}
			}
			$(".left-menu .nav-link:not(.nav-drop)").on('click', function(){
				$(".nav-link.active:not(.nav-drop)").removeClass('active');
				$(".nav-item.active").removeClass('active');
				$(this).addClass('active');
			});
		},
		methods : {
			async logout(){
				this.$swal({
					title: 'Konfirmasi Logout',
					text: "Apakah anda yakin ingin logout ?",
					type: 'warning',
					showCancelButton: true,
					confirmButtonColor: '#9d7b31',
					cancelButtonColor: '#000',
					confirmButtonText: 'Ya',
					cancelButtonText: 'Batal',
				}).then(async (result) => {
					if (result.value) {
						await this.$store.dispatch("auth/authLogout");
						this.$router.replace("/login");
					}
				});
			},
		}
	}
</script>


<style>
	body, html{
		overflow: hidden;
	}
	*{
		font-size: 14px !important;
	}
	.text-main{
		color:var(--main-color) !important;
	}
	.wrapper{
		min-height: 100vh;
		background: var(--grey);
	}
	.back-wrap{
		position: fixed;
		background: var(--main-color);
		width: 100%;
		height: 40vh;
	}
	.img-logo{
		height: 40px;
	}
	.left-menu{
		background: var(--black);
		color: var(--white);
		width: 100%;
		min-height: 91vh;
		overflow-y: auto;
	}
	.img-menu{
		width: 50px !important;
		height: 50px !important;
		border-radius: 50%;
		object-fit: cover;
	}
	.nav .nav-link{
		color: var(--grey);
		width: 100%;
	}
	.sidebar{
		box-shadow: 1px 10px 10px 1px rgba(0,0,0,.1);
	}
	.m-multi{
		margin-left: 29px;
	}
	.nav-item.active > a{
		color: var(--main-color) !important;
	}
	.nav-multi > li{
		padding-left: 23px;
		margin-bottom: 2px;
	}
	.nav-multi > li.active{
		margin-bottom: 2px;
	}
	.nav-multi > li.active a{
		color: var(--main-color) !important;
	}
	.nav-multi > li > .nav-multi > li{
		margin-bottom: 0px;
	}
	.nav-multi > li > .nav-multi > li.active a{
		color: var(--main-color) !important;
	}
	.nav-multi > li > .nav-multi > li > .nav-multi > li{
		background: var(--main-color) !important;
		margin-bottom: 2px;
		border: none;
	}
	.nav-multi > li > .nav-multi > li > .nav-multi > li.active{
		border-left: 4px solid var(--white);
	}
	.nav-multi > li > .nav-multi > li > .nav-multi > li.active a{
		color: var(--white) !important;
	}
	.nav-link.nav-drop{
		position: relative;
	}
	.nav-link.nav-drop.active::after{
		content: '\f078';
	}
	.nav-link.nav-drop::after{
		right: 10px;
		font-size: 12px;
		content: '\f054';
		font-family: 'Font Awesome\ 5 Free';
		font-weight: 900;
		color: var(--main-color);
		position: absolute;
		top: 50%;
		transform: translate(0, -50%);
	}
	.nav-drop+ul{
		display: none;
		opacity: 0;
	}
	.nav-drop+ul.show{
		display: block;
		opacity: 1;
		transition: opacity 1s;
	}
	.navbar-nav li{
		display: inline-block;
		margin-left: 10px;
	}
	.nav-item.active{
		background: var(--black);
	}
	.nav-item.active a{
		color: #fff;
	}
	.credits{
		color: #333;
		position: fixed;
		bottom: 0;
		background: #e2e2e2;
		color: #333;
		font-size: 10px !important;
		width: 80%;
		padding: 10px;
	}
	.col-dln-3{
		font-size: 14px !important;
	}
	.col-dln-9{
		max-height: 85vh;
		overflow-y: auto;
		overflow-x: hidden;
	}
	.p-4{
		padding-right: 0px !important;
	}
	#btn-toggle{
		background: var(--black);
		color: var(--white);
	}
</style>