import { httpClient,  httpClientForm } from './httpClient';

const END_POINT = '/api/status';

const statusAll = () => {
	return httpClient.get( END_POINT + "/");
}
const statusById = (id) => {
	return httpClient.get( END_POINT + "/" + id);
}
const statusAdd = (data) => {
	return httpClient.post( END_POINT + "/", data);
}
const statusUpdate = (id, data) => {
	return httpClient.put( END_POINT + "/" + id, data);
}
const statusDelete = (id) => {
	return httpClient.delete( END_POINT + "/" + id);
}

export {
	statusAll,
	statusById,
	statusAdd,
	statusUpdate,
	statusDelete,
}