import Vue from 'vue'
import VueRouter from 'vue-router'
import store from './stores'

const Dashboard = () => import('@/components/pages/Dashboard.vue')
// Auth
const Login = () => import('@/components/auth/Login.vue')
const Register = () => import('@/components/auth/Register.vue')
const Email = () => import('@/components/auth/Email.vue')
// permit -> technical
const Permit = () => import('@/components/pages/permit/List.vue')
const PermitReport = () => import('@/components/pages/permit/Report.vue')
const PermitReminder = () => import('@/components/pages/permit/Reminder.vue')
const PermitAttachment = () => import('@/components/pages/permit/Attachment.vue')
// reporting
const Reporting = () => import('@/components/pages/reporting/List.vue')
// obligation
const ObligationSingle = () => import('@/components/pages/obligation/single/Index.vue')
const ObligationReport = () => import('@/components/pages/obligation/Report.vue')
const ObligationDues = () => import('@/components/pages/obligation/Dues.vue')
const ObligationDocument = () => import('@/components/pages/obligation/Document.vue')
// user
const UserRole = () => import('@/components/pages/users/Role.vue')
const UserList = () => import('@/components/pages/users/User.vue')
// setting
const AttributeManagement = () => import('@/components/pages/setting/AttributeManagement.vue')

const Birokrasi = () => import('@/components/pages/setting/Birokrasi.vue')
const Index = () => import('@/components/pages/setting/Index.vue')
const Input = () => import('@/components/pages/setting/Input.vue')
const UploadDocument = () => import('@/components/pages/setting/UploadDocument.vue')
const Export = () => import('@/components/pages/setting/Export.vue')
// error
const NotFound = () => import('@/components/pages/error/NotFound.vue')

import { statusAll } from "@/api/status"

Vue.use(VueRouter)
Vue.prototype.$asset = 'https://dolano-tours.github.io/web-statics/'

const checkToken = (to, from, next) => {
	if(store.state.auth.user?.token){
		next();
	}else{
		alert("Sesi sudah tidak valid, silahkan login");
		store.dispatch("auth/authLogout");
		next("/login");
	}
}

const router = new VueRouter({
	// mode : 'history',
	base: process.env.BASE_URL,
	routes : [
	{
		path : '/home', component : Dashboard, name: 'Dashboard',
		beforeEnter: checkToken
	},
	{
		path : '/login', component : Login, name : 'Login'
	},
	{
		path : '/register', component : Register, name : 'Register'
	},
	{
		path : '/email', component : Email, name : 'Email'
	},
	// permit -> technical
	{
		path : '/permit', component : Permit, name : 'Permit',
		beforeEnter: checkToken
	},
	{
		path : '/permit/report/:id', component : PermitReport, name : 'PermitReport', props: true,
		beforeEnter: checkToken
	},
	{
		path : '/permit/attachment/:id', component : PermitAttachment, name : 'PermitAttachment', props: true,
		beforeEnter: checkToken
	},
	{
		path : '/permit/:id/reminder', component : PermitReminder, name : 'PermitReminder', props: true,
		beforeEnter: checkToken
	},
	// reporting
	{
		path : '/reporting', component : Reporting, name : 'Reporting',
		beforeEnter: checkToken
	},
	// obligation
	{
		path : '/obligation/report', component : ObligationReport, name : 'ObligationReport',
		beforeEnter: checkToken
	},
	{
		path : '/obligation/dues', component : ObligationDues, name : 'ObligationDues',
		beforeEnter: checkToken
	},
	{
		path : '/obligation/document', component : ObligationDocument, name : 'ObligationDocument',
		beforeEnter: checkToken
	},
	{
		path : '/obligation/:id/single', component : ObligationSingle, name : 'ObligationSingle', props: true,
		beforeEnter: checkToken
	},
	// users
	{
		path: '/setting/user/role',
		component: UserRole,
		name: 'UserRole',
		beforeEnter: checkToken
	},
	{
		path: '/setting/user/user',
		component: UserList,
		name: 'UserList',
		beforeEnter: checkToken
	},
	// setting
	{
		path: '/setting/attribute-management',
		component: AttributeManagement,
		name: 'AttributeManagement',
		beforeEnter: checkToken
	},
	{
		path : '/setting/birokrasi', component : Birokrasi, name : 'Birokrasi',
		beforeEnter: checkToken
	},
	{
		path : '/setting/index', component : Index, name : 'Index',
		beforeEnter: checkToken
	},
	{
		path : '/setting/input', component : Input, name : 'Input',
		beforeEnter: checkToken
	},
	{
		path : '/setting/upload-document', component : UploadDocument, name : 'UploadDocument',
		beforeEnter: checkToken
	},
	{
		path : '/setting/export', component : Export, name : 'Export',
		beforeEnter: checkToken
	},
	// not found
	{ path: "*", component: NotFound }
	],
	scrollBehavior (to, from, savedPosition) {
		return { x: 0, y: 0 }
	}
})

export default router